
import { Options, Vue, setup } from "vue-class-component";
import { useRouter, Router, useRoute } from "vue-router";
import Input from "@/components/input.vue";
import { formatTime } from "@/utils";
// import WebIM from "easemob-websdk";
import {
  NavBar,
  Icon,
  Popover,
  Grid,
  GridItem,
  Uploader,
  ImagePreview,
  Toast,
  Dialog,
  Field,
  Popup,
  Tag
} from "vant";
import { useStore } from "vuex";
import { getCurrentInstance, ref, onMounted, onUnmounted } from "vue";
import { MSG_TYPE, CHAT_TYPE } from "@/const";
import MsgLeft from "@/components/messageLeft.vue";
import MsgRight from "@/components/messageRight.vue";
import emoji from "@/const/emojs";
import { scrollToBottom } from "@/utils";
import { deliverMsg, formatImFile, createMsg, recallMessage } from "@/utils/im";
import { EasemobChat } from "easemob-websdk/Easemob-chat";
import { MessageBody } from "easemob-websdk/publish/websdk/types/message";
import { AllState } from "@/store";

@Options({
  components: {
    NavBar,
    Input,
    Field,
    Icon,
    MsgLeft,
    MsgRight,
    Popover,
    Grid,
    GridItem,
    Uploader,
    Dialog: Dialog.Component,
    Popup,
    Tag
  }
})
export default class Contact extends Vue {
  chat = setup(() => {
    const router: Router = useRouter();
    const route = useRoute();
    const store = useStore<AllState>();
    const conn = store.state.IM.connect;
    const instance = getCurrentInstance();
    const fromId = route.params.fromId as string;
    const chatType = route.params.chatType as CHAT_TYPE;
    const emojiShow = ref(false);
    const isShowModifyModal = ref<boolean | string>(false);
    const chatId = `${chatType}${fromId}`;
    const modifiedText = ref("");
    const modifiedExt = ref('{"x":1}');
    const modifiedEvent = ref("");
    const modifiedExts = ref('{"x":1}');
    const isShowPinnedMessage = ref(false);
    let pinedMessages = ref<any>([]);
    const previewImage = (url: string) => {
      // TODO: pushurl,
      const imgMsgList = store.state.IM.chat[chatId]?.messageList
        .filter((item: MessageBody) => {
          return item.type === MSG_TYPE.img;
        })
        .map((imgItem: any) => {
          return imgItem.url || imgItem.body.url;
        });

      ImagePreview({
        images: imgMsgList,
        startPosition: imgMsgList.findIndex((imgUrl: string) => {
          return imgUrl === url;
        })
      });
    };

    const previewCombineMsg = (msg: any) => {
      const { url, secret } = msg;
      console.log(url, secret);
      conn
        .downloadAndParseCombineMessage({
          url,
          secret
        })
        .then((res: any) => {
          console.log(res);
        });
    };

    const emojiLs = Object.entries(emoji.obj).map((item) => {
      return {
        name: item[0],
        url: `/emoji/${item[1]}`
      };
    });

    const selectEmoji = () => {
      emojiShow.value = false;
    };

    const toChat = () => {
      router.go(-1);
    };

    // 
    const getHistoryMsg = () => {
      const options = {
        queue: fromId.toLowerCase(),
        isGroup: chatType !== CHAT_TYPE.singleChat,
        count: 10,
        format: true
      };
      //@ts-ignore
      conn.contact.fetchHistoryMessages(options).then((res) => {
        console.log(res, "resres");
        // let uid = window.localStorage.getItem("uid");
        res.forEach((item: any) => {
          store.commit("IM/pushMessage", {
            fromId: chatId,
            message: item
          });
        });
      });
    };

    // 
    conn.addEventHandler("MESSAGE_SCROLL", {
      onTextMessage: () => {
        setTimeout(() => {
          scrollToBottom(document.getElementById("msgWrap"));
        }, 200);
      }
    });

    const onSendClick = () => {
      const ipt: any = instance?.refs.ipt;
      if (ipt.ipt.txt) {
        sendMsg(ipt.ipt.txt);
      } else {
        Toast("");
      }
    };

    // 
    const sendMsg = (txt: string) => {
      let msg: any = createMsg({
        chatType: chatType,
        type: MSG_TYPE.txt,
        to: fromId,
        msg: txt,
        priority: "high",
        msgConfig: {
          // languages: ["zh-CN", "en-US"]
        }
        // ext: {
        // extra: undefined
        // em_apns_ext: {
        //   em_push_title: "custom title",
        //   em_push_content: "custom content"
        // } // iOS
        // }
      });

      // var id = conn.getUniqueId();
      // var msg = new WebIM.message("txt", id);
      // //@ts-ignore
      // msg.set({
      //   msg: txt,
      //   to: fromId,
      //   ext: {
      //     time: "123",
      //     xyz: undefined
      //   }
      // });

      deliverMsg(msg).then((res) => {
        console.log("", msg, res);
        store.commit("IM/pushMessage", {
          fromId: chatId,
          //@ts-ignore
          message: res?.message || msg
        });
        const ipt: any = instance?.refs.ipt;
        ipt.ipt.clear();
        scrollToBottom(document.getElementById("msgWrap"));
      });
    };

    // 
    const sendEmoji = (emoji: string) => {
      emojiShow.value = false;
      const ipt: any = instance?.refs.ipt;
      const emojiStr = `<img class="emojiItem" src="${emoji}"/>`;
      ipt.ipt.mergeTxt(emojiStr);
    };

    const afterReadImg = (file: any) => {
      // URL()
      // Web WebIMConfig.js  useOwnUploadFun: true
      // const imgMsg:any = createMsg({
      //   chatType: chatType,
      //   type: MSG_TYPE.img,
      //   url: "https://www.easemob.com/statics/common/images/logo.png?20211109",
      //   to: fromId,
      //   width: 20,
      //   height: 20,
      // });

      // conn.uploadGroupSharedFile({
      //   groupId: fromId,
      //   file: formatImFile(file.file) as any,
      //   onFileUploadCanceled: () => {
      //     console.log('canceled')
      //   },
      //   onFileUploadComplete: (res) => {
      //        console.log('onFileUploadComplete', res)
      //   },
      //   onFileUploadError: () => {},
      //   onFileUploadProgress: () => {}
      // });

      // conn.uploadGroupSharedFile({
      //   groupId: fromId,
      //   file: formatImFile(file.file) as any,
      // })

      const imgMsg: any = createMsg({
        chatType: chatType,
        type: MSG_TYPE.img,
        to: fromId,
        file: formatImFile(file.file) as any,
        width: 200,
        height: 200,
        thumbnailHeight: 20,
        thumbnailWidth: 20,
        onFileUploadError: function (e) {
          // 
          console.log("onFileUploadError", e);
        },
        onFileUploadProgress: function (progress) {
          // 
          console.log(progress, "progressssss");
        },
        onFileUploadComplete: function () {
          // 
          console.log("onFileUploadComplete");
        }
      });

      console.log(imgMsg, "imgMsgimgMsg");

      // 
      deliverMsg(imgMsg).then((res) => {
        console.log(imgMsg, "imgMsg");
        store.commit("IM/pushMessage", {
          fromId: chatId,
          message: imgMsg
        });
        setTimeout(() => {
          scrollToBottom(document.getElementById("msgWrap"));
        }, 200);
        console.log(res, "");
      });
    };

    const afterReadAttach = (file: any) => {
      // 
      const attachMsg: any = createMsg({
        chatType: chatType,
        type: MSG_TYPE.file,
        to: fromId,
        filename: file.file.name,
        file: formatImFile(file.file) as any,
        onFileUploadError: function () {
          // 
          console.log("onFileUploadError");
        },
        onFileUploadProgress: function (progress) {
          // 
          console.log(progress);
        },
        onFileUploadComplete: function () {
          // 
          console.log("onFileUploadComplete");
        }
      });

      deliverMsg(attachMsg).then((res) => {
        console.log(attachMsg, "attachMsg");
        store.commit("IM/pushMessage", {
          fromId: chatId,
          message: attachMsg
        });
        setTimeout(() => {
          scrollToBottom(document.getElementById("msgWrap"));
        }, 200);
        console.log(res, "");
      });
    };

    const afterReadVideo = (file: any) => {
      // 
      const videoMsg: any = createMsg({
        chatType: chatType,
        type: MSG_TYPE.video,
        to: fromId,
        filename: file.file.name,
        file: formatImFile(file.file) as any,
        onFileUploadError: function () {
          // 
          console.log("onFileUploadError");
        },
        onFileUploadProgress: function (e) {
          // 
          console.log(e);
        },
        onFileUploadComplete: function (data) {
          // 
          console.log("onFileUploadComplete", data);
        }
      });

      deliverMsg(videoMsg).then((res) => {
        console.log(videoMsg, "attachMsg");
        store.commit("IM/pushMessage", {
          fromId: chatId,
          message: videoMsg
        });
        setTimeout(() => {
          scrollToBottom(document.getElementById("msgWrap"));
        }, 200);
        console.log(res, "");
      });
    };

    // 
    const sendCustomMsg = () => {
      const customMsg: any = createMsg({
        chatType: chatType,
        type: MSG_TYPE.custom,
        to: fromId,
        customEvent: "RED_PACKAGE",
        customExts: {
          amount: 1000
        }
      });

      console.log(customMsg, "customMsg");

      deliverMsg(customMsg).then((res) => {
        store.commit("IM/pushMessage", {
          fromId: chatId,
          message: customMsg
        });
        setTimeout(() => {
          scrollToBottom(document.getElementById("msgWrap"));
        }, 200);
        console.log(res, "");
      });
    };

    // 
    const sendCmdMsg = () => {
      const cmdMsg: any = createMsg({
        chatType: chatType,
        type: MSG_TYPE.cmd,
        to: fromId,
        action: "refresh"
      });

      console.log(cmdMsg, "cmdMsg");

      deliverMsg(cmdMsg).then((res) => {
        store.commit("IM/pushMessage", {
          fromId: chatId,
          message: cmdMsg
        });
        setTimeout(() => {
          scrollToBottom(document.getElementById("msgWrap"));
        }, 200);
        console.log(res, "");
      });
    };

    const sendAudioMsg = () => {
      const audioMsg = createMsg({
        type: "audio",
        to: fromId,
        chatType: chatType,
        filename: "audio.mp3",
        body: {
          url: "http://a1-hsb.easemob.com/easemob-demo/sdk111/chatfiles/711702b0-f02e-11ef-9d75-818be9dd88b3?em-redirect=true&share-secret=cRcpwPAuEe-JXR26LuehhUpwVNJzN10gGMC01-yMKPSS9DQj",
          filename: "audio.mp3",
          type: "mp3",
          //@ts-ignore
          length: 10
        }
      });

      deliverMsg(audioMsg).then((res) => {
        store.commit("IM/pushMessage", {
          fromId: chatId,
          message: audioMsg
        });
        setTimeout(() => {
          scrollToBottom(document.getElementById("msgWrap"));
        }, 200);
        console.log(res, "");
      });
    };

    const revokeMsg = (id: string) => {
      const options = {
        mid: id,
        to: fromId,
        chatType: chatType,
        ext: JSON.stringify({
          chatType: chatType
        })
      };
      recallMessage(options).then((res) => {
        store.commit("IM/deleteMessage", { fromId: chatId, id });
        console.log(res, "");
      });
    };

    const showModifyModal = (id: string) => {
      isShowModifyModal.value = id;
    };

    const showPinedMessageList = () => {
      isShowPinnedMessage.value = true;
      getPinnedMessages();
    };

    const modifyMessage = () => {
      let msg = {} as EasemobChat.ModifiedMsg;

      const oldMsg = {
        ...store.state.IM.chat[`${chatType}${fromId}`].messageList.find(
          (item: any) => item.id === isShowModifyModal.value
        )
      };

      console.log("find the modified message", oldMsg);

      const ext = modifiedExt.value ? JSON.parse(modifiedExt.value) : undefined;

      if (oldMsg.type === "txt") {
        msg = createMsg({
          chatType: chatType,
          type: MSG_TYPE.txt,
          to: fromId,
          msg: modifiedText.value,
          msgConfig: {
            // languages: ["zh-CN", "en-US"]
          },
          ext
        }) as EasemobChat.TextMsgBody;
      }

      if (oldMsg.type === "img") {
        msg = createMsg({
          chatType: chatType,
          type: MSG_TYPE.img,
          url: oldMsg.url,
          to: fromId,
          ext: ext
        }) as EasemobChat.ImgMsgBody;
      }

      if (oldMsg.type === "video") {
        //@ts-ignore
        msg = createMsg({
          chatType: chatType,
          type: MSG_TYPE.video,
          to: fromId,
          file: {
            url: oldMsg.url,
            filename: oldMsg.filename,
            filetype: oldMsg.filetype,
            data: oldMsg.data
          },
          //@ts-ignore
          // body: {
          //   url: oldMsg.url
          // },
          ext: ext
        }) as EasemobChat.VideoMsgBody;
      }

      if (oldMsg.type === "file") {
        msg = createMsg({
          chatType: chatType,
          type: MSG_TYPE.file,
          to: fromId,
          //@ts-ignore
          body: {
            url: oldMsg.url
          },
          ext: ext
        }) as EasemobChat.FileMsgBody;
      }

      if (oldMsg.type === "audio") {
        msg = createMsg({
          chatType: chatType,
          type: MSG_TYPE.audio,
          to: fromId,
          //@ts-ignore
          body: {
            url: oldMsg.url
          },
          ext: ext
        }) as EasemobChat.AudioMsgBody;
      }

      if (oldMsg.type === "custom") {
        msg = createMsg({
          chatType: chatType,
          type: MSG_TYPE.custom,
          to: fromId,
          customEvent: modifiedEvent.value || oldMsg.customEvent,
          customExts: JSON.parse(modifiedExts.value) || oldMsg.customExts,
          ext: ext
        }) as EasemobChat.CustomMsgBody;
      }

      if (oldMsg.type === "cmd") {
        msg = createMsg({
          chatType: chatType,
          type: MSG_TYPE.cmd,
          to: fromId,
          action: oldMsg.action,
          ext: ext
        }) as EasemobChat.CmdMsgBody;
      }

      if (oldMsg.type === "combine") {
        msg = createMsg({
          chatType: chatType,
          type: MSG_TYPE.combine,
          to: fromId,
          compatibleText: oldMsg.compatibleText,
          title: oldMsg.title,
          summary: oldMsg.summary,
          messageList: [],
          ext: ext
        }) as unknown as EasemobChat.CombineMsgBody;
      }

      if (oldMsg.type === "loc") {
        msg = createMsg({
          chatType: chatType,
          type: MSG_TYPE.loc,
          to: fromId,
          addr: oldMsg.addr,
          buildingName: oldMsg.buildingName,
          lat: oldMsg.lat,
          lng: oldMsg.lng,
          ext: ext
        }) as EasemobChat.LocationMsgBody;
      }

      conn.contact
        .modifyMessage({
          messageId: isShowModifyModal.value as string,
          modifiedMessage: msg as any
        })
        .then((res) => {
          console.log("modify msg success", res);
          oldMsg.ext = msg.ext;
          if (msg.type === "txt") {
            oldMsg.msg = msg.msg;
          }
          if (msg.type === "custom") {
            oldMsg.customEvent = msg.customEvent;
            oldMsg.customExts = msg.customExts;
          }

          store.commit("IM/updateMessage", {
            fromId: chatId,
            modifiedMsg: oldMsg
          });
        })
        .catch((e) => {
          console.log(e, "modify catch res");
        });

      isShowModifyModal.value = false;
      modifiedText.value = "";
    };

    const pinMessage = (id: string) => {
      conn
        .pinMessage({
          conversationId: fromId,
          conversationType: chatType as "groupChat" | "chatRoom",
          messageId: id
        })
        .then(() => {
          console.log("pinMessage Success");
        })
        .catch((e: any) => {
          console.log(e, "pinMessage failed");
        });
    };

    const unpinMessage = (id: string) => {
      conn
        .unpinMessage({
          conversationId: fromId,
          conversationType: chatType as "groupChat" | "chatRoom",
          messageId: id
        })
        .then(() => {
          console.log("unpinMessage Success");
          store.commit("IM/deletePinedMessage", {
            fromId: `${chatType}${fromId}`,
            id
          });
        })
        .catch((e: any) => {
          console.log(e, "unpinMessage failed");
        });
    };

    // 
    const sendCombineMsg = () => {
      let msg = createMsg({
        chatType: chatType,
        // @ts-ignore
        type: MSG_TYPE.combine,
        to: fromId,
        compatibleText: "[]",
        title: "",
        summary: "",
        messageList: JSON.parse(
          JSON.stringify(store.state.IM.chat[chatId].messageList)
        )
      }) as any;

      deliverMsg(msg).then((res) => {
        console.log(msg, "combine msg");
        store.commit("IM/pushMessage", {
          fromId: chatId,
          message: msg
        });
        setTimeout(() => {
          scrollToBottom(document.getElementById("msgWrap"));
        }, 200);
        console.log(res, "");
      });
    };

    const sendLocationMsg = () => {
      let msg = createMsg({
        chatType: chatType,
        type: MSG_TYPE.loc,
        to: fromId,
        addr: "",
        buildingName: "",
        lat: 39.989,
        lng: 116.309
      });

      deliverMsg(msg).then((res) => {
        console.log(msg, "location msg");
        store.commit("IM/pushMessage", {
          fromId: chatId,
          message: msg
        });
        setTimeout(() => {
          scrollToBottom(document.getElementById("msgWrap"));
        }, 200);
        console.log(res, "");
      });
    };

    // 
    const sendChatReadAck = () => {
      const readAckMsg: any = createMsg({
        type: MSG_TYPE.channel,
        chatType: chatType,
        to: fromId
      });
      deliverMsg(readAckMsg).then((res) => {
        console.log(res, "");
      });
    };

    // 
    const sendMsgReadAck = () => {
      let id =
        store.state.IM.chat[chatId]?.messageList[
          store.state.IM.chat[chatId]?.messageList.length - 1
        ].id;
      const readAckMsg: any = createMsg({
        type: MSG_TYPE.read,
        chatType: chatType as CHAT_TYPE.singleChat | CHAT_TYPE.groupChat,
        id,
        to: fromId
      });

      deliverMsg(readAckMsg).then((res) => {
        console.log(res, "");
      });
    };

    // ack
    const sendGroupMsgAck = () => {
      let id =
        store.state.IM.chat[chatId]?.messageList[
          store.state.IM.chat[chatId]?.messageList.length - 1
        ].id;
      const readAckMsg: any = createMsg({
        type: MSG_TYPE.read,
        chatType: CHAT_TYPE.groupChat,
        id: `${id}`,
        to: fromId,
        ackContent: "group ack content"
      });
      deliverMsg(readAckMsg).then((res) => {
        console.log(res, "");
      });
    };

    const sendAck = () => {
      if (chatType === CHAT_TYPE.groupChat) {
        sendGroupMsgAck();
      } else {
        sendMsgReadAck();
      }
    };

    const getPinnedMessages = () => {
      store.commit("IM/clearPinnedMessage", {
        fromId: chatId
      });
      conn
        .getServerPinnedMessages({
          conversationId: fromId,
          conversationType: chatType as "groupChat" | "chatRoom",
          pageSize: 100
        })
        .then((res: any) => {
          res?.data?.list.forEach((item: any) => {
            item.pinTime = formatTime(
              item.pinTime,
              "YYYY-MM-DD hh:mm:ss"
            ) as unknown as number;
            store.commit("IM/pushPinedMessage", {
              fromId: chatId,
              message: item
            });
          });
          console.log(res, "getServerPinnedMessages");
        })
        .catch((e: any) => {
          console.log(e, "getServerPinnedMessages failed");
        });
    };

    onMounted(() => {
      // 
      // sendChatReadAck();
      if (
        !store.state.IM.chat[chatId] ||
        store.state.IM.chat[chatId]?.messageList.length === 0
      ) {
        getHistoryMsg();
      }

      if (chatType === "chatRoom") {
        conn.chatroom
          .joinChatRoom({
            roomId: fromId
          })
          .then((res) => {
            console.log("", res);
          })
          .catch((e) => {
            console.log(e, "");
          });
      }
    });

    onUnmounted(() => {
      if (chatType === "chatRoom") {
        conn.chatroom.leaveChatRoom({
          roomId: fromId
        });
      }
    });

    return {
      fromId: fromId,
      currentUser: conn.user,
      emojiShow,
      emojiLs: emojiLs,
      chatType: chatType,
      chatInfo: store.state.IM.chat,
      pinInfo: store.state.IM.pin,
      pinMessageIdsMap: store.state.IM.pinMessageIdsMap,
      isShowModifyModal,
      showPinedMessageList,
      isShowPinnedMessage,
      pinedMessages,
      modifiedText,
      modifiedExt,
      modifiedEvent,
      modifiedExts,
      toChat,
      sendMsg,
      selectEmoji,
      sendEmoji,
      afterReadImg,
      afterReadAttach,
      previewImage,
      previewCombineMsg,
      afterReadVideo,
      onSendClick,
      sendCustomMsg,
      sendCmdMsg,
      sendAudioMsg,
      revokeMsg,
      modifyMessage,
      pinMessage,
      unpinMessage,
      showModifyModal,
      sendChatReadAck,
      sendMsgReadAck,
      getHistoryMsg,
      sendGroupMsgAck,
      sendCombineMsg,
      sendLocationMsg,
      getPinnedMessages,
      sendAck
    };
  });
}
